.hero-img{
  width: 100%;
  background-color: rgba(203, 162, 212, 0.2);
  height: 60vh;
  position: relative;
}

.hero-img::before {
  content: "";
  background: url(https://images.unsplash.com/photo-1518665750801-883c188a660d?ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D&auto=format&fit=crop&w=1674&q=80);
  background-size: cover;
  background-position: center-top;
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  background-color: rgb(218, 140, 218);
  opacity: 0.45;
}

.heading{
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.hero-img h1{
  font-size: 2.4rem;
}

.hero-img p{
  font-size: 1.4rem;
  text-align: center;
}

@media screen and (max-width: 640px) {
  .hero-img h1{
    font-size: 2rem;
  }
}

